
import { mapState } from 'vuex'
import XNumber from 'vux/src/components/x-number/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'


export default {
  created () {
    const $api = this.$api
    const $store = this.$store
    const address = $api.address
    address.query().then(list => {
      for (let i = 0; i < list.length; i++) {
        let item = list[i]
        if (item.isDefault) {
          $store.commit('address:selected', {data: item})
        }
      }
    })
  },
  beforeDestroy () {
    let $store = this.$store
    $store.commit('selected', {data: {}})
  },
  components: {
    XNumber,
    XButton,
    Card,
    Group,
    Cell,
    Icon
  },
  methods: {
    select () {
      let self = this
      let $router = this.$router
      self
      $router.push({name: 'Address', params: { select: true }})
    },
    submit () {
      let self = this
      let integral = self.userInfo.dlUserAccount.integral
      if (integral < self.price) {
        self.alert(`积分不足，暂时不能兑换`)
      }
    },
    alert (message) {
      const alert = this.$vux.alert
      alert.show({
        title: '提示',
        content: message
      })
    }
  },
  computed: {
    ...mapState({
      address: state => state.address.data,
      userInfo: state => state.user.userInfo
    })
  },
  data () {
    return {
      title: '',
      price: 3200,
      count: 1,
      thumb: 'static/img_2254.png'
    }
  }
}



