
import _ from 'lodash'
import Vue from 'vue'
import isMobilePhone from 'validator/lib/isMobilePhone'
import Box from 'vux/src/components/box/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'


const USERTYPE = 1
const SENDTYPE = 1
const COUNT_TIME = 80
const storage = window.localStorage

const search = () => {
  let obj = {}
  location.search.replace(/[`?`&]([^=#]+)=([^&#]*)/g, ($1, $2, $3) => {
    obj[$2] = obj[$2] ? obj[$2] + ',' + $3 : $3
  })
  return obj
}

export default {
  components: {
    Box,
    Group,
    Cell,
    Icon,
    XInput,
    XButton,
    Countdown
  },
  methods: {
    finish (index) {
      let self = this
      self.start = false
      self.time = COUNT_TIME
    },
    sigin () {
      const self = this
      const $http = this.$http
      const router = this.$router
      const alert = this.$vux.alert
      let fields = [
        'mobile',
        'password',
        'code',
        'openId'
      ]
      let data = _.merge({
        userType: USERTYPE
      }, _.pick(self.$data, fields))
      if (!data.code) {
        self.alert('请输入验证码')
        return
      }
      if (!data.password) {
        self.alert('请输入密码')
        return
      }
      $http.post('a/api/bund', data).then(res => {
        let response = res.body
        let userInfo = response.results
        if (response.status.index === '10000') {
          storage.setItem('token', response.token)
          storage.setItem('userInfo', JSON.stringify(userInfo))
          router.push('me')
        } else {
          alert.show({
            title: '提示',
            content: response.status.info
          })
        }
      })
    },
    getAuthCode () {
      const self = this
      const $http = this.$http
      let fields = [
        'mobile'
      ]
      let data = _.merge({
        userType: USERTYPE,
        secret: '1i9PivUeseAuThorDL17',
        sendType: SENDTYPE
      }, _.pick(self.$data, fields))
      if (!isMobilePhone(data.mobile, 'zh-CN')) {
        self.alert('请输入正确的手机号')
        return
      }
      if (!self.start) {
        self.start = true
        $http.post(`a/api/smsAuthCode/${self.mobile}`, data, res => {})
      } else {
        self.step += 1
      }
    },
    alert (message) {
      const alert = this.$vux.alert
      alert.show({
        title: '提示',
        content: message
      })
    }
  },
  data () {
    return {
      mobile: '',
      password: '',
      code: '',
      repassword: '',
      start: false,
      time: COUNT_TIME
    }
  },
  beforeRouteEnter (to, from, next) {
    let $http = Vue.http
    let openId = storage.getItem('openId')
    let redirect = search()['redirect'] || '/me'
    if (redirect === 'wechat') {
      redirect = true
    }
    console.log(search())
    if (openId) {
      $http.post('a/api/wxlogin', {openId: openId}).then(res => {
        let response = res.body
        let userInfo = response.results
        if (response.status.index === '10000') {
          storage.setItem('token', response.token)
          storage.setItem('userInfo', JSON.stringify(userInfo))
          next(redirect)
        } else {
          next()
        }
      })
    } else {
      next()
    }
  }
}



