
import Box from 'vux/src/components/box/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'


export default {
  created () {
    let self = this
    self.show = true
    self.load()
  },
  components: {
    Box,
    Card,
    Group,
    Cell,
    Icon,
    Spinner
  },
  methods: {
    load () {
      let self = this
      let $http = this.$http
      $http.post(`a/api/msgDetail/list`, {}).then(res => {
        let response = res.body
        let list = response.results.list
        setTimeout(() => {
          self.list = list
          self.loading = false
        }, 1000)
      })
    }
  },
  computed: {
    show: () => {
      return this.loading || this.list.length === 0
    }
  },
  data () {
    return {
      title: '',
      list: [],
      loading: true
    }
  }
}



